import './App.css';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import SwiperCore, { Navigation, Pagination, A11y, Autoplay, EffectFade } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { useMediaQuery } from 'react-responsive';
import { slide as Menu } from 'react-burger-menu';
// import './swiper/swiper.scss';

function App() {
  const Link_Style = {color: 'white', textDecoration: 'none', fontWeight: 'bold'};
  // const pagelist = ['Home', 'Products', 'About Us'];
  const isMobile = useMediaQuery({ query: `(max-width: 1000px)` });
  var menuBar = "";

  const toggleMenu = ({ isOpen }) => {
    const menuWrap = document.querySelector(".bm-menu-wrap");
    isOpen
      ? menuWrap.setAttribute("aria-hidden", false)
      : menuWrap.setAttribute("aria-hidden", true);
  };

  const closeMenu = () => {
    const close_btn = document.getElementById('react-burger-cross-btn');
    close_btn.click();
  }

  if(isMobile){
    menuBar = 
      <div>
          <Menu right onStateChange={toggleMenu} pageWrapId={ "page-wrap" } outerContainerId={"coverBox"} >
            <main id="page-wrap">
              <Link style={Link_Style} to="/gallery" onClick={closeMenu}>Home</Link>
              <Link style={Link_Style} to="/gallery/products" onClick={closeMenu}>Locations</Link>
              <Link style={Link_Style} to="/gallery/aboutus" onClick={closeMenu}>About Us</Link>
            </main>
          </Menu>
      </div>
      ;
  }
  else {
    menuBar = <ul className="nav_item">
                <li>
                  <Link style={Link_Style} to="/gallery">Home</Link>
                </li>
                <li>
                  <Link style={Link_Style} to="/gallery/products">Locations</Link>
                </li>
                <li>
                  <Link style={Link_Style} to="/gallery/aboutus">About Us</Link>
                </li>
              </ul>
              ;
  }

  return (
    <Router>
    <div id="coverBox" className="nav_bar">
      <nav>
        <h1>Art_1</h1>
        { menuBar }
      </nav>
      <Switch>
        <Route path="/gallery/products">
          <Products />
        </Route>
        <Route path="/gallery/aboutus">
          <AboutUs />
        </Route>
        <Route path="/gallery" exact>
          <Home />
        </Route>
      </Switch>
    </div>
    </Router>
  );
}

const Home = () => {
  SwiperCore.use([Navigation, Pagination, A11y, Autoplay, EffectFade]);
  const isMobile = useMediaQuery({ query: `(max-width: 500px)` });
  var slide_1, slide_2, slide_3, slide_4;
  if(isMobile){
    slide_1 = "https://www.diariodepolly.com/wp-content/uploads/2016/05/asakusa-toquio-7.jpg";
    slide_2 = "https://maijourneys.com/wp-content/uploads/2020/11/Autumn-in-Japan-10.png";
    slide_3 = "https://www.mariefranceasia.com/wp-content/uploads/sites/7/2017/01/12pm-shibuya.jpg";
    slide_4 = "https://images.pexels.com/photos/1798631/pexels-photo-1798631.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500";
  }
  else {
    slide_1 = "https://www.alexisjetsets.com/wp-content/uploads/2019/01/tokyo-sensoji-temple-alexisjetsets-e1547465076859.jpg";
    slide_2 = "https://media.cntraveler.com/photos/5e71171c31d7fe000813ff60/16:9/w_2560%2Cc_limit/Shinjuku-2020-GettyImages-1011208334.jpg";
    slide_3 = "https://hospitality-on.com/sites/default/files/2019-02/shibuyatokyo.jpg";
    slide_4 = "https://static01.nyt.com/images/2020/11/15/business/15biz-JAPAN-ECON/merlin_178322025_2f1cae5e-8276-405a-a394-a039f009a101-superJumbo.jpg";
  }

  return (
    <Swiper
      slidesPerView={1}
      navigation
      autoplay={{ delay: 9000 }}
      pagination={{ clickable: true }}
      effect="fade"
      // onSwiper={(swiper) => console.log(swiper)}
      // onSlideChange={() => console.log('slide change')}
    >
      <SwiperSlide>
        <img src={ slide_1 } alt="slide_1"/>
        <div className="title_cover"><h1>Sensoji, Tokyo</h1></div>
      </SwiperSlide>
      <SwiperSlide>
        <img src={ slide_2 } alt="" />
        <div className="title_cover"><h1>Shinjuku, Tokyo</h1></div>
      </SwiperSlide>
      <SwiperSlide>
        <img src={ slide_3 } alt="" />
        <div className="title_cover"><h1>Shibuya, Tokyo</h1></div>
      </SwiperSlide>
      <SwiperSlide>
        <img src={ slide_4 } alt="" />
        <div className="title_cover"><h1>Dotonbori, Osaka</h1></div>
      </SwiperSlide>
    </Swiper>
  );  
}

function Products() {

  return (
    <div className="product_page">
      <DisplayBox />
    </div>
  );
}

function AboutUs() {
  return (
    <div className="aboutus_page">
      <h1>About Us</h1>
      <img src="https://static.vecteezy.com/system/resources/previews/000/284/266/non_2x/samurai-mask-clip-art-vector.jpg" alt="Samurai Head" />

      <p>
        We are a group of travelers who love to vist Japan. This page is just a starting point that we will keep update and provide more information for those people who love to know more about Japan culture and environment. You may vist more Japan travel information in <a href="https://www.japan.travel/" alt="Japan Travel Website" target="_blank" rel="noopener noreferrer">Here</a>.

        <br /><br />

        It's a wonderful and amazing place for people to visit no matter in which aspect like food, temple or even tranditional crafts. There is so many places for you to discover and explore and it might be the best place to travel with familiy or friends.
      </p>
    </div>
  );
}

const DisplayBox = () => {

  var display_array = [];
  const [locationData, setLocationData] = useState([]);

  useEffect(()=>{

    axios.get('https://demofg.com/rex_fs/Location.json', {
      headers: {'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Headers': 'Content-Type, Authorization',}
    })
    .then(res => {
      
      console.log(res.data)
      setLocationData(res.data.locations);

    })
    .catch(err => console.log(err));

  }, []);

  const locationsLength = locationData.length;

  locationData.map(eachLocation => {
    display_array.push(
      <div key={eachLocation.id} className="display_box">
        <div className="location_title_cover">
          <h1 className="img_title">{eachLocation.name}</h1>
        </div>
        <img src={eachLocation.img_src} alt={eachLocation.name} />
      </div>
    );
    return display_array;
  });

  if(locationsLength === 0){console.log("Empty Records!");}

  return display_array;

};

export default App;

